@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;600&display=swap);
::selection{background-color:var(--selection);color:var(--selection-color)}html,body{font-family:"Roboto", sans-serif;font-weight:300;letter-spacing:1px;background-color:var(--body-background-color);color:var(--body-color);transition:0.5s all}.testimonial{background-color:var(--secondary);border-radius:8px}.my-masonry-grid{display:-webkit-flex;display:flex;width:auto}.my-masonry-grid_column{background-clip:padding-box}.my-masonry-grid_column>div{background-color:var(--secondary);color:var(--light);margin:0px 20px 20px 0px;border-radius:4px;overflow:hidden}

.color-primary{color:var(--primary)}.color-hover-primary:hover{color:var(--primary)}.background-color-primary{background-color:var(--primary)}.background-color-hover-primary:hover{background-color:var(--primary)}.color-secondary{color:var(--secondary)}.color-hover-secondary:hover{color:var(--secondary)}.background-color-secondary{background-color:var(--secondary)}.background-color-hover-secondary:hover{background-color:var(--secondary)}.color-light{color:var(--light)}.color-hover-light:hover{color:var(--light)}.background-color-light{background-color:var(--light)}.background-color-hover-light:hover{background-color:var(--light)}.color-dark{color:var(--dark)}.color-hover-dark:hover{color:var(--dark)}.background-color-dark{background-color:var(--dark)}.background-color-hover-dark:hover{background-color:var(--dark)}.color-selection{color:var(--selection)}.color-hover-selection:hover{color:var(--selection)}.background-color-selection{background-color:var(--selection)}.background-color-hover-selection:hover{background-color:var(--selection)}::selection{background:var(--selection)}.height-100vh{min-height:100vh}a{padding:4px;border-radius:4px;color:var(--light);background-color:var(--secondary);transition:0.1s all}a:hover{text-decoration:none;color:var(--light);background-color:var(--primary)}.footer-version{color:var(--dark);background-color:var(--light)}.footer-credits{font-size:14px}.footer-social-link{font-size:14px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.footer-social-link a{color:var(--primary);background-color:transparent !important}.footer-social-link a:hover{text-decoration:underline;color:var(--primary)}ul{list-style-type:none;padding:0px}.intro{font-size:26px}.main-heading{color:var(--body-color)}.text-bold{font-weight:600}.project-tags{margin:0px}.project-tags li{background-color:var(--primary);font-size:12px;margin:2px;padding:6px;border-radius:4px;color:var(--light);display:inline;text-transform:capitalize}.btn-custom{padding:8px;font-size:14px}.btn-custom.btn-custom-primary{background-color:var(--primary)}.btn-custom.btn-custom-secondary{background-color:var(--secondary)}.btn-custom.btn-custom-alternate{background-color:var(--alternate)}.carousel-control-prev,.carousel-control-next{margin-top:20px;height:30px;width:30px;border-radius:100%;transition:0.25s all;background-color:var(--light);color:var(--primary)}.carousel-control-prev:hover,.carousel-control-next:hover{color:var(--primary);background-color:var(--light)}.carousel-control-prev:focus,.carousel-control-next:focus{color:var(--primary)}.carousel-indicators{margin-bottom:10px}.carousel-indicators li{background-color:var(--light);height:10px;width:10px;border-radius:100%}.testimonial-heading{font-size:16px}.testimonial-card{color:var(--light);height:220px}.testimonial-card .name{font-size:16px}.testimonial-card .quote{font-size:14px}.testimonial-card .avatar{margin:0 auto;height:70px;width:70px;background-size:cover;border-radius:100%}.project-image{background-size:cover;background-position:center;background-repeat:no-repeat;width:100%;height:200px;background-color:var(--light)}.scroll-top{border:none;font-size:25px;position:fixed;margin-top:-130px;margin-left:-70px;background-color:var(--primary);color:var(--light);border-radius:4px}.cursor-pointer{cursor:pointer}

